.card {
	width: 7%;
	height: 7%;
	margin: 1%;
	padding: 5px;
	flex: 1 0 19%;
	text-align: center;
	background-color: white;
}

.image {
	width: 100%;
	height: 100%;
}

@media only screen and (min-width: 1000px) {
	.card {
		flex: 0 0 40%;
		height: 125px;
		padding: 10px 10px 11% 10px;
	}
}